import {
  Box,
  Flex,
  Avatar,
  HStack,
  Link,
  IconButton,
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  useDisclosure,
  useColorModeValue,
  Stack,
  Container,
  Heading,
  Divider
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';


const Links = ['Панель', 'Заказы'];

const NavLink = ({ children }) => (    <Link
      px={2}
      py={1}
      rounded={'lg'}
      _hover={{
        textDecoration: 'none',
        bg: useColorModeValue('gray.200', 'gray.700'),
      }}
      href={'#'}>
      {children}
    </Link>
  );

function Simple() {

    const { isOpen, onOpen, onClose } = useDisclosure();
  
    return (
      <>
        <Box px={7}>
          <Flex h={20} alignItems={'center'} justifyContent={'space-between'}>
            <IconButton
              size={'md'}
              icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
              aria-label={'Open Menu'}
              display={{ md: 'none' }}
              onClick={isOpen ? onClose : onOpen}
            />
            <HStack spacing={8} alignItems={'center'}>
              <Box><strong>FlyXRaid</strong></Box>
              <HStack
                as={'nav'}
                spacing={4}
                display={{ base: 'none', md: 'flex' }}>
                {Links.map((link) => (
                  <NavLink key={link}>{link}</NavLink>
                ))}
              </HStack>
            </HStack>
            <Flex alignItems={'center'}>
              <Menu>
                <MenuButton
                  as={Button}
                  rounded={'full'}
                  variant={'link'}
                  cursor={'pointer'}
                  minW={0}>
                  <Avatar
                    size={'sm'}
                    src={
                      'https://cdn-icons-png.flaticon.com/512/149/149071.png'
                    }
                  />
                </MenuButton>
                <MenuList 
                >
                  <MenuItem>Настройки</MenuItem>
                  <MenuDivider />
                  <MenuItem>Выйти</MenuItem>
                </MenuList>
              </Menu>
            </Flex>
          </Flex>
  
          {isOpen ? (
            <Box pb={4} display={{ md: 'none' }}>
              <Stack as={'nav'} spacing={4}>
                {Links.map((link) => (
                  <NavLink key={link}>{link}</NavLink>
                ))}
              </Stack>
            </Box>
          ) : null}
        </Box>
        <Divider/>



        <Container maxW="6xl">

          <Box zIndex={99} paddingBottom={[5, 10]} maxW="2xl" m="0 auto">

            <Heading as="h2" textAlign="center" fontSize="3xl" mt="30px">
              Доступные услуги
            </Heading>



          </Box>


        </Container>
  
      </>
    );
  }

export default Simple;