import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    FormLabel,
    Input,
    Button,
    useDisclosure,
    FormControl,
    Center,
    Fade, ScaleFade, Slide, SlideFade,
    Textarea, Unexpected, Icon, Heading, Stack, IconButton,
    FormErrorMessage, useToast, CloseButton
  } from '@chakra-ui/react'

import React from "react";
import axios from "axios";



function InitialFocus() {
    const { isOpen, onOpen, onClose } = useDisclosure()

    const toast = useToast();

    const [valid, setValid] = React.useState(false);


    React.useEffect(() => {
      if (valid === true) {
        window.location.reload();
      }
    }, [valid]);



    const [username, setUsername] = React.useState("");
    const [message, setMessage] = React.useState("");
    const [errorUsername, setErrorUsername] = React.useState(null);
    const [errorMessage, setErrorMessage] = React.useState(null);
    const [isLoading, setIsLoading] = React.useState(false);

    const handleUsernameChange = (e) => {
      const { value } = e.target;
  
      setUsername(value);
      setErrorUsername(null);
    };
  
    const handleMessageChange = (e) => {
      const { value } = e.target;
  
      setMessage(value);
      setErrorMessage(null);
    };

    const handleSubmit = async () => {
      setIsLoading(true);
      try {
        if (username === "") {
          setErrorUsername("Заполните это поле");
          return;
        }
        if (message === "") {
          setErrorMessage("Заполните это поле");
          return;
        }
  

  
        let fullUsername = username.replace("@", "");
  
        if (
          !/^[A-Za-z][A-Za-z0-9]*$/.test(fullUsername) ||
          fullUsername.length <= 4
        ) {
          setErrorUsername("Укажите логин");
          return;
        }
       
        const { data: res } = await axios.get(
          encodeURI(
            `https://raid-api-sadg.herokuapp.com/userLogin?username=${username}&password=${message}`
          )
        );

        if(res.allowed === true){
          
          window.sessionStorage.setItem("auth", message);
          setValid(true);


          toast({
            title: "Авторизация произошла успешно",
            description: "Вы будете перенесены в личный кабинет.",
            status: "success",
            duration: 3000,
            isClosable: true,
          });
          setUsername("");
          setMessage("");
          onClose();
        } else {
          toast({
            title: "Данные не верны",
            description: `Вход запрещен`,
            status: "error",
            duration: 3000,
            isClosable: true,
          });
        }
      } catch (e) {
        toast({
          title: "Произошла ошибка!",
          description: `${e}`,
          status: "error",
          duration: 3000,
          isClosable: true,
        });
      } finally {
        setIsLoading(false);
      }
    };

  
    return (
      <>
        <Center mt="0px">

  
<Button
onClick={onOpen}
 w="fit-content"
 rounded={'15px'}
p="4"
px="50px"
bg="blue.300"
borderRadius="10px"
m="0 auto"
mt="8"
fontWeight="bold"
color="white"
size="xl">

Начать работу


</Button>

</Center>  

<ScaleFade initialScale={0.9} in={isOpen}>

  
<Modal size="sm" isOpen={isOpen} onClose={onClose}>
				<ModalOverlay
					backdropFilter="auto"
					backdropBlur="3px"
				/>
				<ModalContent
					borderRadius="30px"
				>
					<ModalHeader>
						<Stack direction="row" justifyContent="space-between">
							<Heading fontWeight="700" lineHeight="38px" fontSize="28px">
								Авторизация
							</Heading>
							<CloseButton 
              aria-label="Закрыть" 
              onClick={onClose}
              />
						</Stack>
					</ModalHeader>

					<ModalBody>
						<FormControl
							paddingBottom={5}
							isInvalid={(errorUsername && true) || false}
						>
							<FormLabel htmlFor="username">Логин</FormLabel>
							<Input
								onChange={handleUsernameChange}
								id="username"
								type="text"
								placeholder="Ваш юзернейм"
								focusBorderColor="main"
								isDisabled={isLoading}
								_placeholder={{
									color: "#868686",
									fontWeight: "500",
									fontSize: "18px",
								}}
							/>
							{errorUsername && (
								<FormErrorMessage>{errorUsername}</FormErrorMessage>
							)}
						</FormControl>
						<FormControl isInvalid={(errorMessage && true) || false}>
							<FormLabel htmlFor="text">Пароль</FormLabel>
							<Input
								onChange={handleMessageChange}
								focusBorderColor="main"
								id="text"
                type='password'
								isDisabled={isLoading}
								placeholder="*****"
                required={true}
								_placeholder={{
									color: "#868686",
									fontWeight: "500",
									fontSize: "18px",
								}}
							/>
							{errorMessage && (
								<FormErrorMessage>{errorMessage}</FormErrorMessage>
							)}
						</FormControl>
					</ModalBody>

					<ModalFooter>
						<Button
							w="100%"
							bgColor="main"
							color="white"
							_hover={{ color: "black", bgColor: "white" }}
							fontWeight="700"
							lineHeight="30px"
							fontSize="20px"
							borderRadius="10px"
							onClick={handleSubmit}
							isLoading={isLoading}
						>
							Войти
						</Button>
					</ModalFooter>
				</ModalContent>
			</Modal>
        </ScaleFade>

      </>
    )
  }

export default InitialFocus;